import React, { useRef } from 'react';
import { useRender } from 'react-zdog';
import ShapeExtrusion from './ShapeExtrusion';

const spacing = (o) => (i, v) => ({ x: 0, y: Math.sin(v * Math.PI * 2 + o) * 20, z: i * 2 });
const sideColor = (v) => `rgb(${v * 255}, ${0}, ${0})`;

const AnimatedShapeExtrusion = (props) => {
    let _spacing = spacing(0);
    const ref = useRef(undefined);
    useRender(() => {
        _spacing = spacing(Date.now() / 2000);
    });

    return (
        <ShapeExtrusion ref={ref} spacing={spacing} sideColor={sideColor} {...props} />
    )
}

export default AnimatedShapeExtrusion;